import { Link, useOutletContext, useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { Input } from "../../components/FormElements";
import axios from "axios";
import { useState } from "react";

function Checkout() {
  const { cartData } = useOutletContext();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState("");
  const [submitError, setSubmitError] = useState("");

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: "onTouched",
  });
  const navigate = useNavigate();

  const onSubmit = async (data) => {
    setIsSubmitting(true);
    setSubmitMessage("");
    setSubmitError("");

    try {
      const { name, email, tel, address } = data;
      const form = {
        data: {
          user: {
            name,
            email,
            tel,
            address,
          },
        },
      };

      const res = await axios.post(
        `/v2/api/${process.env.REACT_APP_API_PATH}/order`,
        form
      );

      console.log(":", res);

      // 
      if (res.data.success) {
        setSubmitMessage(`${res.data.orderId}`);
        reset(); // 

        // 
        navigate(`/success/${res.data.orderId}`);
      } else {
        setSubmitError(res.data.message || "");
      }
    } catch (error) {
      console.error(":", error);

      // 
      if (error.response) {
        // 
        const status = error.response.status;
        const message = error.response.data?.message || "";

        switch (status) {
          case 404:
            setSubmitError("API ");
            break;
          case 400:
            setSubmitError("");
            break;
          case 500:
            setSubmitError("");
            break;
          default:
            setSubmitError(` ${status}: ${message}`);
        }
      } else if (error.request) {
        // 
        setSubmitError("");
      } else {
        // 
        setSubmitError("");
      }
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="bg-light pt-5 pb-7">
      <div className="container">
        <div className="row justify-content-center flex-md-row flex-column-reverse">
          <form className="col-md-6" onSubmit={handleSubmit(onSubmit)}>
            <div className="bg-white p-4">
              <h4 className="fw-bold"></h4>

              {/*  */}
              {submitMessage && (
                <div className="alert alert-success" role="alert">
                  {submitMessage}
                </div>
              )}

              {submitError && (
                <div className="alert alert-danger" role="alert">
                  {submitError}
                </div>
              )}

              <div className="mb-2">
                <Input
                  id="email"
                  labelText="Email"
                  type="email"
                  className="form-control rounded-0"
                  placeholder="example@gmail.com"
                  errors={errors}
                  register={register}
                  rules={{
                    required: "Email ",
                    pattern: {
                      value: /^\S+@\S+$/i,
                      message: "Email ",
                    },
                  }}
                />
              </div>

              <div className="mb-2">
                <Input
                  id="name"
                  labelText=""
                  type="text"
                  className="form-control rounded-0"
                  placeholder="Carmen A. Rose"
                  errors={errors}
                  register={register}
                  rules={{
                    required: "",
                    maxLength: {
                      value: 10,
                      message: " 10",
                    },
                  }}
                />
              </div>

              <div className="mb-2">
                <Input
                  id="tel"
                  labelText=""
                  type="tel"
                  className="form-control rounded-0"
                  placeholder="0933-123-123"
                  errors={errors}
                  register={register}
                  rules={{
                    required: "",
                    minLength: {
                      value: 6,
                      message: " 6 ",
                    },
                    maxLength: {
                      value: 12,
                      message: " 12 ",
                    },
                  }}
                />
              </div>

              <div className="mb-2">
                <Input
                  id="address"
                  labelText=""
                  type="text"
                  className="form-control rounded-0 mt-1"
                  placeholder="Address"
                  errors={errors}
                  register={register}
                  rules={{
                    required: "",
                  }}
                />
              </div>
            </div>

            <div className="d-flex flex-column-reverse flex-md-row mt-4 justify-content-between align-items-md-center align-items-end w-100">
              <Link to="/cart" className="btn btn-outline-secondary me-2">
                
              </Link>
              <button
                type="submit"
                className="btn btn-primary"
                disabled={isSubmitting || !cartData?.carts?.length}
              >
                {isSubmitting ? (
                  <>
                    <span
                      className="spinner-border spinner-border-sm me-2"
                      role="status"
                      aria-hidden="true"
                    ></span>
                    ...
                  </>
                ) : (
                  ""
                )}
              </button>
            </div>
          </form>

          <div className="col-md-4">
            <div className="bg-white p-4 mb-4">
              <h4 className="mb-4"></h4>
              {cartData?.carts?.length > 0 ? (
                <>
                  {cartData.carts.map((item) => (
                    <div className="d-flex mb-3" key={item.id}>
                      <img
                        src={item.product.imageUrl}
                        alt={item.product.title}
                        className="me-2"
                        style={{
                          width: "48px",
                          height: "48px",
                          objectFit: "cover",
                        }}
                      />
                      <div className="w-100">
                        <div className="d-flex justify-content-between fw-bold">
                          <p className="mb-0">{item.product.title}</p>
                          <p className="mb-0">x {item.qty}</p>
                        </div>
                        <div className="d-flex justify-content-between text-muted">
                          <small>: NT$ {item.product.price}</small>
                          <small>: NT$ {item.final_total}</small>
                        </div>
                      </div>
                    </div>
                  ))}
                  <hr />
                  <div className="d-flex justify-content-between mt-4">
                    <p className="mb-0 h4 fw-bold">Total</p>
                    <p className="mb-0 h4 fw-bold">
                      NT$ {cartData.final_total}
                    </p>
                  </div>
                </>
              ) : (
                <div className="text-center py-4">
                  <p className="text-muted"></p>
                  <Link to="/products" className="btn btn-primary">
                    
                  </Link>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Checkout;
